import React from 'react';
import styled from 'styled-components';
import { Item, Link } from '../Styled/Link';

const NavMenu = styled.ul`
    grid-column: 3/span 8;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    justify-content: space-evenly;
    padding: 0 10px;
    @media (max-width: 768px) {
        grid-column: 1/span 12;
        grid-row: 2;
        justify-content: space-between;
        padding: 0;
    }
`;

const HeadLink = styled(Link)`
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
    :hover {
        color: #3595F6;
    }
    @media (max-width: 768px) {
        font-size: 14px;
        line-height: 16px;
    }
`;
const NavItem = props => (
    <Item>
        <HeadLink href={props.link}>{props.name1}<br/>{props.name2}</HeadLink>
    </Item>
)

const NavBar = () => (
        <NavMenu>
            <NavItem link="#" name1="–ü–æ–¥–æ–±—Ä–∞—Ç—å" name2="–∫–≤–∞—Ä—Ç–∏—Ä—É"/>
            <NavItem link="#" name1="–ó–∞–±—Ä–æ–Ω–∏—Ä–æ–≤–∞—Ç—å" name2="–∫–≤–∞—Ä—Ç–∏—Ä—É"/>
            <NavItem link="#" name1="–•–æ–¥" name2="—Å—Ç—Ä–æ–∏—Ç–µ–ª—å—Å—Ç–≤–∞"/>
            <NavItem link="#" name1="–ü—Ä–æ–µ–∫—Ç–Ω–∞—è" name2="–¥–µ–∫–ª–∞—Ä–∞—Ü–∏—è"/>
        </NavMenu>
)
export default NavBar;